import React, { useEffect, useRef, useState } from "react";
import WordCloud from "wordcloud";
import data from "../data/data.json";

const WordCloudComponent = ({ onWordClick }) => {
  const canvasRef = useRef(null);
  const [wordList, setWordList] = useState([]);
  const [searchInput, setSearchInput] = useState("");
  const [searchResult, setSearchResult] = useState("");

  useEffect(() => {
    // Preprocess text data to extract word frequencies
    const wordCounts = {};
    data.forEach((item) => {
      const words = item.statement.toLowerCase().split(/\W+/);
      words.forEach((word) => {
        if (word && word.length > 3) {
          wordCounts[word] = (wordCounts[word] || 0) + 1;
        }
      });
    });

    const wordArray = Object.entries(wordCounts).map(([word, count]) => [word, count]);
    setWordList(wordArray);

    // Render the Word Cloud
    WordCloud(canvasRef.current, {
      list: wordArray,
      gridSize: 8,
      weightFactor: 15,
      fontFamily: "Arial",
      color: () => `hsl(${Math.random() * 360}, 100%, 50%)`,
      rotateRatio: 0.5,
      backgroundColor: "#f5f5f5",
      click: (item) => onWordClick(item[0]),
    });
  }, [onWordClick]);

  // Handle Search
  const handleSearch = () => {
    const foundWord = wordList.find(([word]) => word.toLowerCase() === searchInput.toLowerCase());
    if (foundWord) {
      setSearchResult(`The word "${foundWord[0]}" appears ${foundWord[1]} times.`);
      // Trigger the same logic as clicking a word in the Word Cloud
      onWordClick(foundWord[0]);
    } else {
      setSearchResult(`The word "${searchInput}" was not found.`);
    }
  };

  return (
    <div>
      <h2>Interactive Word Cloud</h2>

      {/* Search Bar */}
      <div style={{ marginBottom: "20px" }}>
        <input
          type="text"
          placeholder="Search for a word..."
          value={searchInput}
          onChange={(e) => setSearchInput(e.target.value)}
          style={{ padding: "10px", width: "200px", marginRight: "10px" }}
        />
        <button
          onClick={handleSearch}
          style={{ padding: "10px", backgroundColor: "#007bff", color: "#fff", border: "none" }}
        >
          Search
        </button>
      </div>

      {/* Search Result */}
      {searchResult && <p style={{ marginTop: "10px" }}>{searchResult}</p>}

      {/* Word Cloud Canvas */}
      <canvas ref={canvasRef} width={800} height={600} />
    </div>
  );
};

export default WordCloudComponent;
