import React, { useState } from "react";
import WordCloudComponent from "./components/WordCloudComponent";
import InteractiveBubbleChart from "./components/InteractiveBubbleChart";
import FilterableTable from "./components/FilterableTable"; // Added for filter table view

function App() {
  const [showBubbleChart, setShowBubbleChart] = useState(false);
  const [clickedWord, setClickedWord] = useState("");
  const [showTable, setShowTable] = useState(false);

  const handleShowInsights = () => {
    setShowBubbleChart(true); // Show Bubble Chart
  };

  const handleBackToHome = () => {
    setShowBubbleChart(false); // Show Home Screen
    setClickedWord("");
    setShowTable(false);
  };

  const handleWordClick = (word) => {
    setClickedWord(word); // Set the clicked word
    setShowTable(true); // Show the Filterable Table
  };

  const handleBackToWordCloud = () => {
    setShowTable(false); // Return to Word Cloud
    setClickedWord("");
  };

  return (
    <div style={{ textAlign: "center", padding: "20px" }}>
      <h1>Fact-Check Explorer</h1>

      {/* Conditional Rendering */}
      {!showBubbleChart ? (
        <>
          {/* Home View */}
          {!showTable ? (
            <>
              <WordCloudComponent onWordClick={handleWordClick} />
              <button
                onClick={handleShowInsights}
                style={{
                  marginTop: "20px",
                  padding: "10px 20px",
                  fontSize: "16px",
                  backgroundColor: "#007BFF",
                  color: "white",
                  border: "none",
                  borderRadius: "5px",
                  cursor: "pointer",
                }}
              >
                Claim Insights Visualizer
              </button>
            </>
          ) : (
            <>
              <button
                onClick={handleBackToWordCloud}
                style={{
                  marginBottom: "20px",
                  padding: "10px 20px",
                  fontSize: "16px",
                  backgroundColor: "#FFC107",
                  color: "white",
                  border: "none",
                  borderRadius: "5px",
                  cursor: "pointer",
                }}
              >
                ← Back to Word Cloud
              </button>
              <FilterableTable filterWord={clickedWord} />
            </>
          )}
        </>
      ) : (
        <>
          {/* Bubble Chart View */}
          <button
            onClick={handleBackToHome}
            style={{
              marginBottom: "20px",
              padding: "10px 20px",
              fontSize: "16px",
              backgroundColor: "#28A745",
              color: "white",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
            }}
          >
            ← Back to Home
          </button>
          <InteractiveBubbleChart />
        </>
      )}
    </div>
  );
}

export default App;
