import React, { useMemo, useState } from "react";
import { useTable } from "react-table";
import Modal from "react-modal";
import Sentiment from "sentiment";
import "chart.js/auto";
import { Bar, Doughnut } from "react-chartjs-2";
import data from "../data/data.json";

Modal.setAppElement("#root");

const FilterableTable = ({ filterWord }) => {
  const [selectedClaim, setSelectedClaim] = useState(null);
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [votes, setVotes] = useState({});

  const sentimentAnalyzer = useMemo(() => new Sentiment(), []);
  const verdictOptions = ["true", "mostly-true", "half-true", "false", "pants-fire"];

  const colorMapping = {
    "true": "#28a745",
    "mostly-true": "#17a2b8",
    "half-true": "#ffc107",
    "false": "#dc3545",
    "pants-fire": "#fd7e14",
  };

  const analyzedData = useMemo(() => {
    return data.map((item) => {
      const sentimentResult = sentimentAnalyzer.analyze(item.statement);
      const score = sentimentResult.score;
      let sentimentCategory = "Neutral";
      if (score > 0) sentimentCategory = "Positive";
      else if (score < 0) sentimentCategory = "Negative";

      return { ...item, sentiment: sentimentCategory };
    });
  }, [sentimentAnalyzer]);

  const filteredData = useMemo(() => {
    return analyzedData.filter((item) =>
      filterWord ? item.statement.toLowerCase().includes(filterWord.toLowerCase()) : true
    );
  }, [filterWord, analyzedData]);

  const getVerdictChartData = () => {
    const counts = filteredData.reduce((acc, item) => {
      acc[item.verdict] = (acc[item.verdict] || 0) + 1;
      return acc;
    }, {});
    return {
      labels: Object.keys(counts),
      datasets: [
        {
          label: "Claims by Verdict",
          data: Object.values(counts),
          backgroundColor: Object.keys(counts).map((verdict) => colorMapping[verdict]),
        },
      ],
    };
  };

  const getSourceChartData = () => {
    const counts = filteredData.reduce((acc, item) => {
      acc[item.statement_source] = (acc[item.statement_source] || 0) + 1;
      return acc;
    }, {});
    return {
      labels: Object.keys(counts),
      datasets: [
        {
          label: "Claims by Source",
          data: Object.values(counts),
          backgroundColor: "#ff6f61",
        },
      ],
    };
  };

  const getSentimentStackedData = () => {
    const sentimentCounts = {};
    filteredData.forEach((item) => {
      const source = item.statement_source || "Unknown";
      if (!sentimentCounts[source]) {
        sentimentCounts[source] = { Positive: 0, Neutral: 0, Negative: 0 };
      }
      sentimentCounts[source][item.sentiment]++;
    });

    return {
      labels: Object.keys(sentimentCounts),
      datasets: [
        {
          label: "Positive",
          backgroundColor: "#28a745",
          data: Object.values(sentimentCounts).map((item) => item.Positive),
        },
        {
          label: "Neutral",
          backgroundColor: "#ffc107",
          data: Object.values(sentimentCounts).map((item) => item.Neutral),
        },
        {
          label: "Negative",
          backgroundColor: "#dc3545",
          data: Object.values(sentimentCounts).map((item) => item.Negative),
        },
      ],
    };
  };

  const getDonutChartData = (claim) => {
    const userVote = votes[claim.statement] || "No Vote";
    const actualVerdict = claim.verdict;

    return {
      labels: ["Actual Verdict", "User Vote"],
      datasets: [
        {
          data: [50, 50],
          backgroundColor: [colorMapping[actualVerdict] || "#ccc", colorMapping[userVote] || "#6c757d"],
        },
      ],
    };
  };

  const handleRowClick = (claim) => {
    setSelectedClaim(claim);
    setModalIsOpen(true);
  };

  const handleVote = (claimId, vote) => {
    setVotes((prev) => ({ ...prev, [claimId]: vote }));
  };

  const columns = useMemo(
    () => [
      { Header: "Verdict", accessor: "verdict" },
      { Header: "Originator", accessor: "statement_originator" },
      { Header: "Statement", accessor: "statement" },
      { Header: "Source", accessor: "statement_source" },
      { Header: "Date", accessor: "statement_date" },
      { Header: "Sentiment", accessor: "sentiment" },
    ],
    []
  );

  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } = useTable({
    columns,
    data: filteredData,
  });

  return (
    <div>
      {/* Charts Above Table */}
      <div style={{ display: "flex", justifyContent: "space-between", marginBottom: "20px" }}>
        <div style={{ width: "48%" }}>
          <h3>Claims by Verdict</h3>
          <Bar data={getVerdictChartData()} options={{ responsive: true }} />
        </div>
        <div style={{ width: "48%" }}>
          <h3>Claims by Source</h3>
          <Bar data={getSourceChartData()} options={{ responsive: true }} />
        </div>
      </div>

      {/* Table */}
      <table {...getTableProps()} style={{ width: "100%", border: "1px solid black" }}>
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()} style={{ padding: "10px", border: "1px solid black" }}>
                  {column.render("Header")}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row) => {
            prepareRow(row);
            return (
              <tr
                {...row.getRowProps()}
                style={{ cursor: "pointer" }}
                onClick={() => handleRowClick(row.original)}
              >
                {row.cells.map((cell) => (
                  <td {...cell.getCellProps()} style={{ padding: "10px", border: "1px solid black" }}>
                    {cell.render("Cell")}
                  </td>
                ))}
              </tr>
            );
          })}
        </tbody>
      </table>

      {/* Sentiment Analysis Below Table */}
      <div style={{ marginTop: "20px" }}>
        <h3>Sentiment Analysis by Source</h3>
        <Bar
          data={getSentimentStackedData()}
          options={{
            responsive: true,
            plugins: { legend: { position: "top" } },
            scales: { x: { stacked: true }, y: { stacked: true } },
          }}
        />
      </div>

      {/* Modal */}
      <Modal isOpen={modalIsOpen} onRequestClose={() => setModalIsOpen(false)}>
        {selectedClaim && (
          <div>
            <h2>Claim Details</h2>
            <p><strong>Statement:</strong> {selectedClaim.statement}</p>
            <p><strong>Verdict:</strong> {selectedClaim.verdict}</p>
            <p><strong>Sentiment:</strong> {selectedClaim.sentiment}</p>

            <h3>What do you think?</h3>
            {verdictOptions.map((option) => (
              <button
                key={option}
                style={{
                  margin: "5px",
                  padding: "5px",
                  backgroundColor: colorMapping[option],
                  color: "white",
                  border: "none",
                }}
                onClick={() => handleVote(selectedClaim.statement, option)}
              >
                {option}
              </button>
            ))}

            <div style={{ width: "50%", margin: "20px auto" }}>
              <Doughnut data={getDonutChartData(selectedClaim)} />
            </div>
            <button onClick={() => setModalIsOpen(false)}>Close</button>
          </div>
        )}
      </Modal>
    </div>
  );
};

export default FilterableTable;
