import React, { useState, useEffect } from "react";
import { Bubble } from "react-chartjs-2";
import "chart.js/auto";
import data from "../data/data.json";

const InteractiveBubbleChart = () => {
  const [selectedYear, setSelectedYear] = useState("");
  const [xParameter, setXParameter] = useState("verdict");
  const [yParameter, setYParameter] = useState("statement_originator");
  const [filteredData, setFilteredData] = useState([]);
  const [chartData, setChartData] = useState({ datasets: [] });

  const parameters = {
    verdict: "Verdict",
    statement_originator: "Statement Originator",
    statement_source: "Source",
    statement_date: "Month",
  };

  useEffect(() => {
    // Filter data based on the selected year
    if (selectedYear) {
      const filtered = data.filter(
        (item) => new Date(item.statement_date).getFullYear() === parseInt(selectedYear)
      );
      setFilteredData(filtered);
    }
  }, [selectedYear]);

  useEffect(() => {
    if (filteredData.length > 0) {
      renderChart();
    }
  }, [filteredData, xParameter, yParameter]);

  const renderChart = () => {
    // Dynamically compute unique X and Y values
    const uniqueX = [
      ...new Set(filteredData.map((item) => item[xParameter] || "N/A")),
    ];
    const uniqueY = [
      ...new Set(filteredData.map((item) => item[yParameter] || "N/A")),
    ];

    // Map data to bubble chart format
    const bubbleData = [];
    uniqueX.forEach((xValue, i) => {
      uniqueY.forEach((yValue, j) => {
        const count = filteredData.filter(
          (item) =>
            (item[xParameter] || "N/A") === xValue &&
            (item[yParameter] || "N/A") === yValue
        ).length;

        if (count > 0) {
          bubbleData.push({
            x: i, // X index
            y: j, // Y index
            r: count * 5, // Bubble size (adjust scale)
            label: `${parameters[xParameter]}: ${xValue}, ${parameters[yParameter]}: ${yValue}, Claims: ${count}`,
          });
        }
      });
    });

    setChartData({
      datasets: [
        {
          label: "Bubble Chart",
          data: bubbleData,
          backgroundColor: "rgba(54, 162, 235, 0.5)",
          borderColor: "rgba(54, 162, 235, 1)",
        },
      ],
    });
  };

  return (
    <div style={{ textAlign: "center", padding: "20px" }}>
      <h2>Interactive Bubble Chart</h2>
      <div>
        {/* Dropdowns for Year, X-axis, Y-axis */}
        <label>Select Year: </label>
        <select
          value={selectedYear}
          onChange={(e) => setSelectedYear(e.target.value)}
        >
          <option value="">-- Select Year --</option>
          {[...new Set(data.map((item) => new Date(item.statement_date).getFullYear()))]
            .sort()
            .map((year) => (
              <option key={year} value={year}>
                {year}
              </option>
            ))}
        </select>

        <label style={{ marginLeft: "10px" }}>Parameter 1 (X-axis): </label>
        <select
          value={xParameter}
          onChange={(e) => setXParameter(e.target.value)}
        >
          {Object.entries(parameters).map(([key, value]) => (
            <option key={key} value={key}>
              {value}
            </option>
          ))}
        </select>

        <label style={{ marginLeft: "10px" }}>Parameter 2 (Y-axis): </label>
        <select
          value={yParameter}
          onChange={(e) => setYParameter(e.target.value)}
        >
          {Object.entries(parameters).map(([key, value]) => (
            <option key={key} value={key}>
              {value}
            </option>
          ))}
        </select>
      </div>

      {/* Render Bubble Chart */}
      {selectedYear && (
        <div style={{ marginTop: "20px" }}>
          <Bubble
            data={chartData}
            options={{
              scales: {
                x: {
                  ticks: {
                    callback: (val) => {
                      const xKeys = [
                        ...new Set(
                          filteredData.map((item) => item[xParameter] || "N/A")
                        ),
                      ];
                      return xKeys[val] || "";
                    },
                  },
                  title: { display: true, text: parameters[xParameter] },
                },
                y: {
                  ticks: {
                    callback: (val) => {
                      const yKeys = [
                        ...new Set(
                          filteredData.map((item) => item[yParameter] || "N/A")
                        ),
                      ];
                      return yKeys[val] || "";
                    },
                  },
                  title: { display: true, text: parameters[yParameter] },
                },
              },
              plugins: {
                tooltip: {
                  callbacks: {
                    label: (context) => {
                      const { raw } = context;
                      return raw.label; // Display the label for each bubble
                    },
                  },
                },
              },
            }}
          />
        </div>
      )}
    </div>
  );
};

export default InteractiveBubbleChart;
